<template>
  <Content :style="content">
    <div v-if="show">
      <Divider dashed orientation="left">
        <h2>{{ currentPage }}</h2>
      </Divider>
      <TableZ @is-check="onIsCheck" ref="tableData"></TableZ>
      <!-- 三个按钮 -->
      <div class="button-group">
        <createBotton :value="value" @add="onAdd" ></createBotton>
        <updateBotton></updateBotton>
        <deleteBotton @remove="onRemove"></deleteBotton>
      </div>
    </div>
    <Divider dashed orientation="left" v-else>
      <h2>{{ currentPage }}</h2>
    </Divider>
  </Content>
</template>

<style lang="scss" scoped>
.button-group {
  margin-top: 10px;
  button {
    margin-right: 10px;
  }
}
</style>

<script>
import TableZ from '../TableZ.vue'
import { mapActions } from 'vuex'
import buttons from '../buttons'
export default {
  name: 'departmentRegister',
  components: {
    TableZ,
    createBotton: buttons.createBotton,
    deleteBotton: buttons.deleteBotton,
    updateBotton: buttons.updateBotton
  },
  props: ['currentPage', 'show'],
  data: function() {
    return {
      content: {
        // 这里是content的边框设计
        padding: '24px',
        minHeight: '85vh',
        background: 'rgb(255,255,255)',
        border: '1px solid transparent',
        'border-radius': '6px',
        'box-shadow': '0 2px 7px rgba(0,0,0,.15)'
      },
      value: {
        code: '',
        name: ''
      },
      index: []
    }
  },
  computed: {
    functionC() {
      return ''
    }
  },
  methods: {
    ...mapActions(['actionUpdatePassWord']),
    onAdd() {
      let code = this.value.code
      let name = this.value.name
      this.$refs.tableData.data.push({ code, name })
      this.$Message.success('追加完了')
    },
    onRemove() {
      if (this.index.length === 0) {
        return this.$Message.error('削除失败!')
      }
      let data = this.index
        .concat(this.$refs.tableData.data)
        .filter(function(v, i, arr) {
          return arr.indexOf(v) === arr.lastIndexOf(v)
        })
      if (data) {
        this.$refs.tableData.data = data
        this.index = []
        console.log(this.index)
      }
      this.$Message.success('削除完了')
    },
    onIsCheck: function(data, isOk) {
      if (isOk) {
        let index = [...this.index, data]
        this.index = [...new Set(index)]

        // 点一下checkbox就改了vuex里的password
        // const newPassWord = { password: '9527' }
        // this.actionUpdatePassWord(newPassWord).then(() => {
        //   this.$Message.success('パスワード修正完了')
        //   console.log(this.passWord)
        // })
      }
      if (isOk === false) {
        this.index = this.index.filter(e => e !== data)
      }
    }
  }
}
</script>
