<template>
  <div>
    <Button class="add-botton" type="success" @click="addModal = true" icon="md-add">追加</Button>
    <Modal v-model="addModal">
      <Divider dashed orientation="left" style="margin-bottom: 30px">所属登録</Divider>
      <Input
        v-model="value.code"
        size="large"
        placeholder="コード"
        style="margin-bottom: 10px; width:95%"
      />
      <Input
        v-model="value.name"
        size="large"
        placeholder="表示名称"
        style="margin-bottom: 10px; width:95%"
      />
      <div slot="footer">
        <Button type="text" @click="cancel">キャンセル</Button>
        <Button type="success" size="large" @click="add">追加</Button>
      </div>
    </Modal>
  </div>
</template>

<style lang="scss" scoped>
.add-botton {
  position: relative;
  top: 2.7rem;
  right: 7.5rem;
}
</style>

<script>
export default {
  name: 'createBotton',
  props: ['value'],
  components: {},
  data: function() {
    return {
      addModal: false
    }
  },
  computed: {
    functionC() {
      return ''
    }
  },
  methods: {
    add() {
      this.$emit('add')
      this.addModal = false
    },
    cancel() {
      if (this.addModal) {
        this.addModal = false
      }
    }
  }
}
</script>
