<template>
  <Header :style="{padding: 0}" class="layout-header-bar">
    <Icon
      @click.native="collapsedSider"
      :class="rotateIcon"
      :style="{margin: '0 20px'}"
      type="md-menu"
      size="24"
    ></Icon>
  </Header>
</template>

<style lang="scss" scoped>
.layout-header-bar {
  color: $white;
  box-shadow: 0 1px 1px $box-shadow;
  text-align: left;
}
.rotate-icon {
  transform: rotate(-90deg);
}
</style>

<script>
export default {
  name: 'vHeader',
  props: ['isCollapsed'],
  components: {},
  data: function() {
    return {}
  },
  computed: {
    rotateIcon() {
      return ['menu-icon', this.isCollapsed ? 'rotate-icon' : '']
    }
  },
  methods: {
    collapsedSider() {
      this.$emit('collapsedSider')
    }
  }
}
</script>
