<template>
  <div>
    <Button class="deleteBotton" type="error" @click="deleteModal = true" icon="md-trash">削除</Button>
    <Modal v-model="deleteModal">
      <Divider dashed orientation="left" style="margin-bottom: 30px">注意</Divider>
      <h2 style="color: rgb(252,13,27); position:relative; top:-6px">削除してよろしいでしょうか</h2>
      <div slot="footer">
        <Button type="text" @click="cancel">キャンセル</Button>
        <Button type="error" size="large" @click="remove">削除</Button>
      </div>
    </Modal>
  </div>
</template>

<style lang="scss" scoped>
.deleteBotton {
  position: relative;
  top: -32px;
  left: 7.5rem;
}
</style>

<script>
export default {
  name: 'deleteBotton',
  components: {},
  data: function() {
    return {
      deleteModal: false
    }
  },
  computed: {
    functionC() {
      return ''
    }
  },
  methods: {
    remove() {
      this.$emit('remove')
      this.deleteModal = false
    },
    cancel() {
      if (this.deleteModal) {
        this.deleteModal = false
      }
    }
  }
}
</script>
