<template>
  <div class="layout">
    <Layout>
        <vHeader :isCollapsed="isCollapsed" @collapsedSider="onSider"></vHeader>
        <Layout>
          <Sider ref="side1" hide-trigger collapsible :collapsed-width="0" v-model="isCollapsed">
            <Menu theme="light" width="auto" @on-select="handleContent">
              <Submenu v-for="(guider, index) of guiders" :key="index" :name="guider.name">
                <template slot="title">
                <Icon :type="guider.type"/>{{ guider.name }}
                </template>
                <MenuItem v-for="(value, childIndex) of guider.child" :key="index +'-' + childIndex" :name="value.name">{{ value.name }}</MenuItem>
              </Submenu>
            </Menu>
          </Sider>
        <Layout>
            <div :style="{margin: '20px', background: '#fff'}">
              <departmentRegister :currentPage="currentPage" :show="show"></departmentRegister>
              <!--  将此处的content以动态以组件的形式 -->
              <!-- <component :is="departmentRegister"></component> -->
            </div>
            <Footer class="layout-footer-center">1970-2019 &copy; ArtSparks</Footer>
        </Layout>
      </Layout>
    </Layout>
  </div>
</template>
<style lang="scss" scoped>
.layout-footer-center {
  text-align: center;
}
</style>
<script>
import vHeader from '../components/Header/vHeader'
import Contents from '../components/Contents'
export default {
  name: 'HomePage',
  components: {
    vHeader,
    departmentRegister: Contents.departmentRegister
  },
  data() {
    return {
      guiders: [
        {
          name: '随時処理',
          child: [{}],
          type: 'ios-navigate'
        },
        {
          name: '申請承認',
          child: [{ name: '申請承認' }, { name: '申請状況一覧' }],
          type: 'ios-paper'
        },
        {
          name: '月次処理',
          child: [{}],
          type: 'ios-calendar'
        },
        {
          name: '有休管理',
          child: [{}],
          type: 'ios-cloudy-night'
        },
        {
          name: '登録管理',
          child: [
            { name: '社員区分登録' },
            { name: '勤務体系登録' },
            { name: '所属登録' },
            { name: '社員登録・ICカード割当' },
            { name: '勤怠管理対象外社員登録' },
            { name: '所属長兼任設定' },
            { name: '祝日登録' },
            { name: '時間休設定' },
            { name: 'ICカード登録要求一覧' },
            { name: '端末登録' },
            { name: '環境設定' }
          ],
          type: 'ios-albums'
        }
      ],
      currentPage: '登録管理の所属登録を選択してください',
      currentContent: '',
      show: false,
      isCollapsed: false
    }
  },
  methods: {
    // 这个方法里补充动态组件逻辑
    // dynamicContent() {
    // },
    handleContent(menu) {
      if (menu) {
        if (menu === '所属登録') {
          this.currentPage = '所属登録'
          return (this.show = true)
        }
      }
      this.show = false
      return (this.currentPage =
        'この機能がまだ開発中ので、再選択してください。')
    },
    onSider() {
      this.$refs.side1.toggleCollapse()
    }
  }
}
</script>
